.char-card {
    width: 100%;
    height: auto;
    border-radius: 4px;
    border: 1px solid #cacaca;
    background: white;
    -webkit-transition: box-shadow 0.2s;
    transition: box-shadow 0.2s;
    box-shadow: 0px 0px 3px grey;
}

.char-card img {
    width: calc(100% - 1em);
    margin: 0.5em;
}

.char-card:hover {
    box-shadow: 6px 6px 6px grey;
}

.char-card:active {
    box-shadow: 4px 4px 3px grey;
}
p {
    display: inline-block;
    color: white;
    font-family: 'Russo One', sans-serif;
    font-size: 120%;
    padding-top: 0.75em;
}

.title {
    font-size: 200%;
    margin: 0.75em 0 0 0;
    padding: 0;
}


.header {
    text-align: center;
    display: grid;
    grid-column-gap: 1em;
    grid-row-gap: 1em;
    grid-template-columns: 30% 30% 30%;
    -webkit-justify-content: center;
            justify-content: center;
    height: 11vh;
    position: fixed;
    top: 0;
    width: 100%;
    background: #9595b8;
    background-image: -webkit-linear-gradient(#181F29, #363C48);
    background-image: linear-gradient(#181F29, #363C48);
    box-shadow: 0px 0px 50px #4e4e4e;
}

    
.char-list {
    display: grid;
    grid-column-gap: 1em;
    grid-row-gap: 1em;
    grid-template-columns: 30% 30% 30%;
    width: 60%;
    margin: auto;
    margin-top: 10vh;
    background-image:  url(data:image/png;base64,);
    justify-items: center;
    padding: 2em 1em 2em 1em;
    -webkit-justify-content: center;
            justify-content: center;
    box-shadow: 0px 0px 50px #4e4e4e;

}

body {
    margin: 0;
}

html {
    background-image: url(/ClickyGame/static/media/gplaypattern.b73669ad.png);
}
